<template>
  <div>
    <el-dialog title="发起提问" :visible="qrShow" width="40%" center @close="close">
      <el-input type="textarea" :rows="10" placeholder="请输入内容" v-model="textarea" maxlength="200" show-word-limit></el-input>
      <div class="icon-text">
        <span class="iconfont icon-tupianguanli"></span>
        <span>上传图片</span>
      </div>
      <div class="picture-wrp">
        <div class="upload-wrp" @click="triggerClick">
          <div class="img-preview" v-for="(file,i) in fileList" :key="i">
            <img :src="file.src">
          </div>
          <div class="inputBoild">
            <span class="iconfont icon-add" id="iconAdd"></span>
            <input
              type="file"
              id="fileInput"
              ref="upload"
              accept="image/jpeg, image/png, image/jpg"
              multiple
              @change="uploadChange"
            >
          </div>
        </div>

        <!-- <Upload  ref="upload" :config="config"></Upload> -->
      </div>
      <span slot="footer" class="dialog-footer">
        <el-button
          type="info"
          @click="centerDialogVisible"
          style="width:332px;height:50px;background:#bead95"
        >发布</el-button>
      </span>
    </el-dialog>
  </div>
</template>
<script>
export default {
  data() {
    return {
      qrShow: false,
      textarea: "",
      // dialogVisible: false,
      // dialogImageUrl: "",
      sessionKey: "",
      fileList: []
    };
  },
  methods: {
    uploadChange(val) {
      console.log(val);
      const _this = this;
      let formData = new FormData();
      let files = this.$refs.upload.files;
      let url = "";
      let fileList = [];
      var type = "";
      formData.append('file' ,this.$refs.upload.files );
      for (var i = 0; i < files.length; i++) {
        formData.append("file[" + i + "]", files[i]);
        let blob =
          window.navigator.userAgent.indexOf("Chrome") >= 1 ||
          window.navigator.userAgent.indexOf("Safari") >= 1
            ? window.webkitURL.createObjectURL(files[i])
            : window.URL.createObjectURL(files[i]);
        let img = {
          name: files[i].name,
          src: blob
        };
        if (files[i].name.indexOf("video") > 0) {
          type = "video";
        } else {
          type = "classic";
        }
        fileList.push(img);
      }
      let config = {
        headers: { "Content-Type": "multipart/form-data" }
      };
      // if (type == "video") {
      //   url = this.APIURL + "/bench/classic/material/image/video_image";
      // } else {
      //   url = this.APIURL + "/bench/classic/material/image/classic_image";
      // }
      // this.$axios.post(url, formData).then(function({ data }){
      //   _this.fileList = fileList;
      //   console.log(data);
      // });
    },
    triggerClick() {
      this.$refs.upload.click();
    },
    //发布问题
    clickOut(type) {
      var url = this.APIURL + "/classic/question/save";
      var _this = this;
      let data = {
        content: this.textarea,
        anonymity: 0,
        type: type
        // heartfelt:0,
      };
      this.$axios
        .post(url, data, { headers: { SessionKey: this.SessionKey,key:this.key }})
        .then(function(res) {
          console.log(res.data);
          if (res.data.errorCode == 0) {
            _this.$message({
              message: "发布成功",
              type: "success"
            });
            _this.$store.commit("setQadidlogShow", false);
          } else {
            alert(res.data.msg);
          }
        })
        .catch(function(error) {});
    },
    //点击发布传参Question
    centerDialogVisible() {
      this.clickOut("question");
    },
    close() {
      this.$store.commit("setQadidlogShow", false);
    }
  },
  watch: {
    QadidlogShow: function(val) {
      this.qrShow = val;
    },
  },
  computed: {
    QadidlogShow() {
      return this.$store.state.QadidlogShow;
    },
    SessionKey() {
      return this.$store.state.SessionKey;
    },
    key() {
      return this.$store.state.key;
    }
  }
};
</script>
<style lang="scss">
.icon-text {
  margin: 20px;
  color: #bead95;
  font-size: 16px;
  font-family: PingFang SC;
  span:nth-child(1) {
    font-size: 24px;
    vertical-align: middle;
  }
  span:nth-child(2) {
    font-size: 17px;
    vertical-align: middle;
  }
}
.picture-wrp {
  // padding: 30px 0;
  .upload-wrp {
    // width: 100%;
    // height: 132px;
    // background: #fff;
    // border: 1px solid #ddd;
    display: flex;
    flex-flow: row;
    flex-wrap: wrap;
    .inputBoild {
      width: 130px;
      height: 130px;
      border: 1px dashed #c0ccda;
      #iconAdd {
        color: #8c939d;
        font-size: 28px;
        line-height: 130px;
        display: block;
        font-weight: 400;
        text-align: center;
      }
    }
    .img-preview {
      flex: 0 0 20%;
      img {
        width: 130px;
        height: 130px;
      }
    }
  }
}

#fileInput {
  display: none;
}
</style>

