let updatedBreadCrumb = function (data, level, vm) {
  let curBreadCrumbList = vm.$store.state.breadList
  // 获取
  curBreadCrumbList[level] = data
  for (var i = level + 1; i < curBreadCrumbList.length; i++) {
    curBreadCrumbList[i] = {}
  }
  vm.$store.commit('setBreadList', curBreadCrumbList)
}
export { updatedBreadCrumb }
