<template>
  <div>
    <myBreadcrumb :breadList="breadList" />
    <div class="content">
      <div class="left">
        <div class="card lightest-bg">
          <div class="card-top">
            <div class="tupian light-bg">
              <img :src="list.images ? list.images.detail : ''" alt />
            </div>
            <div class="bofang">
              <div class="name">
                <span class="dark-text">{{ list.title }}</span>
              </div>
              <div class="time">
                <p class="medium-text">{{ list.time }}</p>
              </div>
              <div>
                <p class="medium-text">
                  诵经师:{{ list.declaimer }} &nbsp; 录音师:{{
                    list.mixer
                  }}
                  &nbsp; 监制:{{ list.producer }}
                </p>
              </div>
              <div class="lable">
                <span class="assistant-box">{{ classicLabel }}</span>
              </div>
              <div class="switch lightest-box" @click="handClick">
                <a href="javascript:void(0)">
                  <span
                    :class="
                      isplaying
                        ? 'iconfont icon-zanting push-button theme-text'
                        : 'iconfont icon-bofang push-button theme-text'
                    "
                  ></span>
                  <!-- <span
                    class="iconfont icon-zanting push-button theme-text   iconfont icon-bofang push-button theme-text"
                    v-show="flag"
                  ></span> -->
                </a>
                <a href="javascript:void(0)">
                  <span
                    v-text="btnText"
                    v-show="!flag"
                    class="theme-text"
                  ></span>
                  <span
                    v-text="btnText"
                    v-show="flag"
                    class="theme-text"
                  ></span>
                </a>
              </div>
              <div class="click">
                <ul>
                  <li>
                    <a
                      href="javascript:void(0)"
                      @click="collect"
                      class="assistant-text"
                      title="收藏"
                    >
                      <span
                        class="iconfont icon-shoucang"
                        :class="[list.is_collect == 1 ? 'show' : '']"
                      ></span>
                      <span
                        v-html="list.is_collect == 1 ? '已收藏' : '收藏'"
                      ></span>
                    </a>
                  </li>
                  <li>
                    <a
                      href="javascript:void(0)"
                      @click="zhuyin"
                      class="assistant-text"
                      title="助印"
                    >
                      <span class="iconfont icon-printing"></span>
                      <span>助印</span>
                    </a>
                  </li>
                  <li>
                    <a
                      href="javascript:void(0)"
                      @click="qranswer"
                      class="assistant-text"
                      title="问答"
                    >
                      <span class="iconfont icon-wenda1"></span>
                      <span>问答</span>
                    </a>
                  </li>
                  <li>
                    <a
                      href="javascript:void(0)"
                      @click="reward"
                      class="assistant-text"
                      title="打赏"
                    >
                      <span class="iconfont icon-dashang"></span>
                      <span>打赏</span>
                    </a>
                  </li>
                  <li>
                    <a
                      href="javascript:void(0)"
                      @click="shareIng"
                      class="assistant-text"
                      title="分享"
                    >
                      <span class="iconfont icon-fenxiang"></span>
                      <span>分享</span>
                    </a>
                  </li>
                </ul>
              </div>
              <div class="ewmcode lightest-bg theme-line">
                <QrcodeVue :value="wchatText" class="wchatCode" />
                <p class="dark-text">
                  微信扫一扫
                  <br />收听有声经典
                </p>
              </div>
            </div>
          </div>
          <div class="card-bottom">
            <div class="tables">
              <div class="table">
                <el-table
                  :data="list.chapters"
                  style="width: 100%"
                  @row-click="handdle"
                  class="dark-text"
                  :row-class-name="tableRowClassName"
                >
                  >
                  <el-table-column
                    :label="
                      list.title +
                        '已更新到' +
                        '(' +
                        list.recentChapterTitle +
                        ')'
                    "
                    prop="title"
                  ></el-table-column>
                  <el-table-column align="right">
                    <template slot-scope="scope">
                      <el-button
                        size="mini"
                        type="text"
                        @click="getId(scope.$index, scope.row)"
                        >听与看</el-button
                      >
                      <span class="iconfont icon-wengao dark-text"></span>
                    </template>
                  </el-table-column>
                </el-table>
              </div>
            </div>
          </div>
        </div>
        <div class="textDetails lightest-bg" v-if="isHide">
          <div class="hideBg">
            <span id="text" class="light-text">{{
              list.introduce ? list.introduce : "暂时无数据..."
            }}</span>
          </div>
          <a
            href="javascript:void(0)"
            class="lookMore assistant-text"
            @click="onShow()"
            >展开></a
          >
        </div>
        <div class="textDetails lightest-bg" v-else>
          <div class="showBg">
            <span id="text" class="light-text">{{ list.introduce }}</span>
          </div>
          <a
            href="javascript:void(0)"
            class="lookMore assistant-text"
            @click="onHide()"
            >收起&lt;</a
          >
        </div>
        <!-- <myPlayer/> -->
      </div>
      <div class="right">
        <div class="blank-1"></div>
        <div class="blank-2"></div>
      </div>
    </div>
    <el-dialog
      :title="qrTitle"
      :visible.sync="shareShow"
      id="scanDialoge"
      :close-on-click-modal="false"
      show-close
    >
      <QrcodeVue :value="qrText" size="200" class="qrCode" />
      <!-- <img id="qrCode" :src="imgUrl" alt> -->
      <p>{{ tipInfo }}</p>
    </el-dialog>
    <qrdialog />
  </div>
</template>
<script>
import qrdialog from "../q&Answer/components/qr-dialog";
// 面包屑方法
import { updatedBreadCrumb } from "@/assets/js/util.js";
//二维码
import QrcodeVue from "qrcode.vue";
import myBreadcrumb from "../../components/bread-crumb";
// import myPlayer from "../../components/player";
import { setInterval, setTimeout } from "timers";
export default {
  data() {
    return {
      isplaying: false,
      qrTitle: "",
      tipInfo: "",
      qrText: "",
      shareShow: false,
      classicLabel: "",
      isHide: false,
      list: "",
      id: "",
      btnText: "立即播放",
      button: true,
      title: "",
      isShow: false,
      wchatText: "",
      rowId: ""
    };
  },
  created() {
    let title = sessionStorage.getItem("ClassicLabel");
    let data = {};
    data.path = this.$route.path;
    data.name = title;
    updatedBreadCrumb(data, 1, this);
  },
  mounted() {
    this.detailsList();
  },
  methods: {
    tableRowClassName({ row, rowIndex }) {
      if (row.id === this.rowId) {
        return "current-row";
      } else {
        return "";
      }
    },
    detailsList() {
      var _this = this;
      let cId = localStorage.getItem("ClassicId");
      _this.classicLabel = sessionStorage.getItem("ClassicLabel");
      var url = this.APIURL + "/classic/classic/detail";
      this.$axios
        .post(
          url,
          { classicId: cId, order: "asc" },
          { headers: { SessionKey: this.SessionKey, key: this.key } }
        )
        .then(function({ data }) {
          _this.list = data.data;
          // 如果当前经典是播放经典
          if (_this.audioList.classicId == _this.list.id) {
            _this.isplaying = true;
          } else {
            _this.isplaying = false;
          }

          _this.wchatText =
            "https://m.lihan.ltd/#/pages/mantra/classicDetail?curId=" +
            _this.list.id +
            "&app=true&bgclr=false";
        });
    },
    onShow: function() {
      this.isHide = false; //点击onShow切换为false，显示为展开画面
    },
    onHide: function() {
      this.isHide = true; //点击onHide切换为true，显示为折叠画面
    },
    handClick() {
      var _this = this;
      _this.isplaying = !_this.isplaying;
      var audio = document.getElementById("myAudio");
      this.$store.commit("setBookList", this.list);
      this.$store.commit("setFlag", !_this.flag);
      if (_this.flag == true) {
        // 判断当前state里是否有有效值，如果没有播放当前第一条
        if (this.chapterId) {
          this.$store.commit("setChapterId", this.chapterId);
        } else {
          this.$store.commit("setChapterId", this.list.chapters[0].id);
          _this.rowId = this.list.chapters[0].id;
        }
        _this.btnText = "暂停播放";
        audio.play();
        this.$store.commit("setSeen", true);
      } else {
        this.btnText = "立即播放";
        audio.pause();
      }
    },
    getId(index, row) {
      var _this = this;
      _this.rowId = row.id;
      var Text = document.getElementById("text");
      this.$store.commit("setClassicId", _this.id);
      this.$store.commit("setChapterId", row.id);
      this.$store.commit("setBookList", _this.list);
      this.$store.commit("setSeen", true);
      // setTimeout(function() {
      //   _this.$store.commit("setSeen", false);
      // }, 4000);
    },
    handdle(row, event, column) {
      this.rowId = row.id;
      var _this = this;
      var Text = document.getElementById("text");
      this.$store.commit("setClassicId", _this.id);
      this.$store.commit("setChapterId", row.id);
      this.$store.commit("setBookList", _this.list);
      this.$store.commit("setSeen", true);
    },

    // 收藏
    collect() {
      let cId = localStorage.getItem("ClassicId");
      var url = this.APIURL + "/classic/classic/behavior";
      let data = {
        classicId: cId,
        type: "collect"
      };
      this.$axios
        .post(url, data, {
          headers: { SessionKey: this.SessionKey, key: this.key }
        })
        .then(res => {
          if (res.data.errorCode === 0) {
            this.detailsList();
          } else {
            alert(res.data.msg);
            this.$router.push({ path: "/login" });
          }
        });
    },
    reward() {
      this.shareShow = true;
      this.qrText =
        "https://m.lihan.ltd/#/pages/helpPrint/reward?curId=" + this.list.id;
      this.qrTitle = "微信扫描二维码进行打赏";
    },
    zhuyin() {
      this.shareShow = true;
      this.qrText =
        "https://m.lihan.ltd/#/pages/helpPrint/index?curId=" + this.list.id;
      this.qrTitle = "微信扫描二维码进行助印";
    },
    shareIng() {
      this.shareShow = true;
      this.qrText =
        "https://m.lihan.ltd/#/pages/mantra/classicDetail?curId=" +
        this.list.id +
        "&app=false&bgclr=false";
      this.qrTitle = "微信扫描二维码进行分享";
    },
    //问答对话框
    qranswer() {
      this.$store.commit("setQadidlogShow", true);
    }
  },
  watch: {
    $route: {
      handler(val, oldval) {
        console.log(val); //新路由信息
        console.log(oldval); //老路由信息
      },
      // 深度观察监听
      deep: true
    },
    // flag: function() {
    //   var audio = document.getElementById("myAudio");
    //   if (audio.paused) {
    //     console.log(1);
    //   } else {
    //     console.log(2);
    //   }
    // }
    audioList(val) {
      if (val) {
        this.detailsList();
      }
    }
  },

  computed: {
    audioList() {
      return this.$store.state.audioList;
    },
    flag() {
      return this.$store.state.flag;
    },
    chapterId() {
      return this.$store.state.chapterId;
    },
    classicId() {
      return this.$store.state.classicId;
    },
    SessionKey() {
      return this.$store.state.SessionKey;
    },
    key() {
      return this.$store.state.key;
    },
    breadList() {
      return this.$store.state.breadList;
    }
  },
  components: {
    myBreadcrumb,
    // myPlayer,
    QrcodeVue,
    qrdialog
  }
};
</script>
<style lang="scss">
.content {
  width: 1200px;
  margin: 0 auto;
  margin-top: 22px;
  display: flex;
  .left {
    .card {
      width: 921px;
      margin-bottom: 12px;
      .card-top {
        overflow: hidden;
        position: relative;
        padding: 10px 0;
        .tupian {
          float: left;
          width: 222px;
          height: 222px;
          margin-left: 20px;
          img {
            width: 222px;
            height: 222px;
          }
        }
        .bofang {
          float: left;
          position: relative;
          width: 679px;
          p {
            opacity: 1;
            font-size: 14px;
            font-family: PingFang SC;

            letter-spacing: 0px;
            margin: 0;
            margin: 14px 0 0 60px;
          }
          .name {
            margin-left: 60px;
            span {
              opacity: 1;
              font-size: 28px;
              font-family: Adobe Heiti Std;
              letter-spacing: 0px;
            }
          }
          .lable {
            span {
              display: inline-block;
              width: 90px;
              height: 24px;
              opacity: 1;
              font-size: 14px;
              font-family: PingFang SC;
              letter-spacing: 0px;
              line-height: 24px;
              text-align: center;
            }
            span:nth-child(1) {
              margin: 10px 0 0 62px;
            }
            span:nth-child(2) {
              margin-left: 10px;
            }
          }
          .switch {
            width: 150px;
            height: 40px;
            border: 2px solid;
            border-radius: 6px;
            margin: 14px 0 0 52px;
            position: relative;
            .push-button {
              font-size: 24px;
              position: absolute;
              top: 6px;
              left: 25px;
            }
            span {
              position: absolute;
              top: 8px;
              left: 55px;
              opacity: 1;
              font-size: 16px;
              font-family: PingFang SC;
              letter-spacing: 0px;
            }
          }
          .ewmcode {
            position: absolute;
            top: 50px;
            right: 30px;
            width: 100px;
            border: 1px solid;
            z-index: 9;
            border-radius: 6px;
            .wchatCode {
              width: 70px;
              height: 70px;
              margin: 15px auto 0;
              canvas {
                width: 70px !important;
                height: 70px !important;
              }
            }
            p {
              width: 100%;
              margin: 0;
              text-align: center;
              padding: 10px 0;
              line-height: 20px;
              font-size: 12px;
              font-family: PingFang SC;
            }
          }
        }
        .click {
          width: 500px;
          overflow: hidden;
          margin: 12px 0 0 50px;
          ul {
            margin: 0;
            padding: 0;
            line-height: 36px;

            li {
              list-style: none;
              float: left;
              margin-right: 45px;
              a {
                text-decoration: none;
                font-size: 16px;
                .iconfont {
                  font-size: 16px;
                  margin-right: 3px;
                  // vertical-align: middle;
                }
                .show {
                  color: #584945 !important ;
                }
              }
            }
          }
        }
      }
      .card-bottom {
        overflow: auto;
        width: 100%;
        margin-top: 30px;
        .tables {
          .table {
            background: rgba(255, 255, 255, 1);
          }
        }
      }
      // .iconfont {
      //   vertical-align: middle;
      // }
    }
    .textDetails {
      width: 921px;
      position: relative;
      .hideBg {
        width: 100%;
        padding: 6px 15px;
        overflow: hidden;
        // white-space: nowrap;
        text-overflow: ellipsis;
        text-indent: 2em;
        display: -webkit-box;
        -webkit-line-clamp: 3;
        /* autoprefixer:off */
        -webkit-box-orient: vertical;
        /* autoprefixer:on */
        span {
          opacity: 1;
          font-size: 16px;
          text-indent: 50px;
          font-family: PingFang SC;
          line-height: 28px;
          letter-spacing: 0px;
        }
      }
      .showBg {
        width: 100%;
        text-indent: 2em;
        padding: 6px 15px;
        span {
          opacity: 1;
          font-size: 16px;
          text-align: justify;
          font-family: PingFang SC;
          line-height: 28px;
          letter-spacing: 0px;
        }
      }
      .lookMore {
        text-decoration: none;
        display: block;
        margin-left: 400px;
        margin-top: 10px;
      }
    }
  }
  .right {
    width: 266px;
    height: auto;
    background: #d8d7d7;
    margin-left: 14px;
    .blank-1 {
      width: 100%;
      height: 235px;
      background: #d8d7d7;
    }
    .blank-2 {
      width: 100%;
      opacity: 1;
      background: #d8d7d7;
    }
  }
}

.el-button + .el-button {
  margin-left: 0 !important;
}
.el-button--text {
  color: #333333;
  font-size: 14px;
}
.el-icon-document:before {
  font-size: 16px;
  margin-right: 6px;
}
.el-table__body tr.current-row > td {
  background: #e6e6e6 !important;
}

.el-table__body tr.current-row > td:first-child {
  border-left: 4px solid #584945;
}
.el-button--text:hover,
.el-button--text:focus {
  color: #584945;
}
.el-table thead {
  color: #333333;
}
</style>
